* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  padding-top: 46px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

#search-form {
  z-index: 10;
  width: 100vw;
  height: 46px;
  font: inherit;
  background-color: #384ecb;
  padding: 8px;
  position: fixed;
  top: 0;
  left: 0;
}

.form__box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.form__box:hover, .form__box:focus {
  border: 1px solid #3995dc;
  border-radius: 4px;
}

.form__box input {
  height: 30px;
  border: none;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  outline: none;
  margin-right: -4.39px;
}

.form__box button {
  height: 30px;
  border: none;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 260px;
  justify-content: center;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

.photo-card {
  border-radius: 3px;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.info {
  justify-content: space-between;
  padding: 5px 20px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  margin: 0;
  display: flex;
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.box-button {
  justify-content: center;
  margin-top: 10px;
  display: flex;
}

.load-more {
  width: 150px;
  color: #fafafa;
  text-align: center;
  background-color: #384ecb;
  border: none;
  border-radius: 3px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.is-hidden:not(:focus):not(:active) {
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  height: 1px;
  white-space: nowrap;
  width: 1px;
  position: absolute;
  overflow: hidden;
}

